  font-size: 15px;
  transition: 0.4s;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .accordion__content .accordion.active, .accordion__content .accordion:hover {
    background-color: #ccc; }
  .accordion__content .accordion::after {
    content: "\002B";
