// acordion
.accordion__content {
  .accordion {
    background-color: #eee;
    color: #444;
    font-weight: bold;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: 0.4s;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &.active,
    &:hover {
      background-color: #ccc;
    }
    &::after {
      content: "\002B";
      color: #777;
      font-weight: bold;
      float: right;
      margin-left: 5px;
    }
    &.active {
      &::after {
        content: "\2212";
      }
    }
  }
  .panel {
    padding: 0 18px;
    background-color: white;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
    .descripcion {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
}
